// TODO
//methods must show the respective menus of links

<template>
  <div class="inspire">
    <v-app-bar dark elevate-on-scroll  color="#36213E" class="my-4">

      <v-btn @click.prevent="showItemPersonCards" icon>
        <v-icon>mdi-home-outline</v-icon>
      </v-btn>

      <v-spacer></v-spacer>

      <v-btn @click.prevent="showFilter" icon>
        <v-icon>mdi-filter-outline</v-icon>
      </v-btn>

      <v-btn @click.prevent="showProfileActivities" icon>
        <v-icon>mdi-account-outline</v-icon>
      </v-btn>

      <v-menu>
        <template v-slot:activator="{on, attrs}">
      <v-btn v-bind="attrs" v-on="on" icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
        </template>
      <v-list>
        <v-list-item v-for="option in options" :key="option" @click="activateOption(option.pageName)">
          <v-list-item-title>
            {{option.title}}
          </v-list-item-title>
        </v-list-item>
      </v-list>
      </v-menu>
    </v-app-bar>
    <router-view class = "inspire"/>
  </div>
</template>

<script>
export default {
  name: 'App',
  options: [{title: "Become VIP", pageName: "GetVip"}, {title: "Create business profile", pageName: "CreateProfile"}, {title: "Contact us", pageName: "ContactUs"}, {title: "Take a tour", pageName: "Tour"}],

  methods: {
    activateOption(pageName){
      this.$router.push({name: pageName})
    },
    showItemPersonCards(){
      this.$store.commit("setFilterStatus", false)
      window.location.reload()
    },
    showFilter(){
      this.$router.push({name: "Filter"})
    },
    showProfileActivities(){
      this.$router.push({name: "MyDeals"})
    },
    showOptions(){
      this.$router.push({name: "ContactUs"})
    },
  }
}
</script>

<style>
.inspire{
    background-color: #36213E;
    background: #36213E;
    }
</style>



